.products {
  padding: 2rem;
}

.items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-top: 3rem;
}

.item {
  display: flex;
  flex-direction: column;
  border: 2px solid var(--boulder-100);
  gap: 1rem;
  padding: 1rem;
}

.image-container {
  display: grid;
  place-items: center;
}

.item h2 {
  font-size: 0.9rem;
  color: var(--boulder-900);
}

h2.price {
  color: var(--boulder-500);
}

.item > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
}

.item button {
  padding: 0.3rem 1rem;
  background: none;
  border: 2px solid var(--boulder-900);
  cursor: pointer;
  transition: all 200ms ease-in-out;
}

.item button:hover {
  background: var(--boulder-900);
  color: var(--boulder-50);
}

button.disabled {
  background-color: none;
  color: var(--boulder-900);
  font-size: 0.8rem;
  border: 0;
  font-style: italic;
  pointer-events: none;
}

@media (max-width: 720px) {
  .items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 500px) {
  .items {
    grid-template-columns: 1fr;
  }
}
